<template>
  <div id="main-container">
    <table class="responsive">
      <caption>
        {{ $route.meta.title }}
      </caption>
      <thead>
      <tr>
        <th scope="col">이름</th>
        <th scope="col">타입</th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td data-label="이름">
          <input-text
            name="figureName"
            v-model="figureInForm.figureName"
            :rules="{ required: true}"
          />
        </td>
        <td data-label="타입">
          <input-select
            name="type"
            :options="types"
            v-model="figureInForm.type"
            class="select-box"/>
        </td>
      </tr>
      </tbody>
    </table>
    <table class="responsive">
      <caption>
        기타정보
      </caption>
      <thead>
      <tr>
        <th scope="col">등급</th>
        <th scope="col">별칭</th>
        <th scope="col">버전</th>
        <th scope="col">등장 시리즈</th>
        <th scope="col">세대</th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td data-label="등급">
          <input-select
            name="grade"
            :options="grades"
            v-model="figureInForm.grade"
            class="select-box"/>
        </td>
        <td data-label="별칭">
          <input-text
            name="nickName"
            v-model="figureInForm.nickName"
          />
        </td>
        <td data-label="버전">
          <input-text
            name="version"
            v-model="figureInForm.version"
            />
        </td>
        <td data-label="등장 시리즈">
          <input-text
            name="series"
            v-model="figureInForm.series"
          />
        </td>
        <td data-label="세대">
          <input-text
            name="generation"
            v-model="figureInForm.generation"
            />
        </td>
      </tr>
      </tbody>
    </table>
    <table class="responsive">
      <caption>
        구매정보
      </caption>
      <thead>
      <tr>
        <th scope="col">가격</th>
        <th scope="col">구매일자</th>
        <th scope="col">구매처</th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td data-label="가격">
          <input-text
            name="price"
            v-model="figureInForm.price"
            oninput="this.value = this.value.replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,');"
          />
        </td>
        <td
          data-label="구매일자">
          <DatePicker
            name="date"
            v-model="figureInForm.date"
            placeholder="구매일자"
          >
          </DatePicker>
        </td>
        <td data-label="구매처">
          <input-text
            name="store"
            v-model="figureInForm.store"
          />
        </td>
      </tr>
      </tbody>
    </table>
    <table class="responsive">
      <caption>
        제작정보
      </caption>
      <thead>
      <tr>
        <th scope="col">시작일자</th>
        <th scope="col">마감일자</th>
        <th scope="col">완료여부</th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td
          data-label="시작일자">
          <DatePicker
            name="startDate"
            v-model="figureInForm.startDate"
            placeholder="시작일자"
          >
          </DatePicker>
        </td>
        <td
          data-label="마감일자">
          <DatePicker
            name="endDate"
            v-model="figureInForm.endDate"
            placeholder="마감일자"
          >
          </DatePicker>
        </td>
        <td data-label="완료여부">
          <input-select
            name="completeYn"
            :options="completes"
            v-model="figureInForm.completeYn"
            class="select-box"/>
        </td>
      </tr>
      </tbody>
    </table>
    <div class="input-wrapper" style="margin: 10px 0 20px 0;">
      <button
        @click="_saveWish"
        class="basic__button"
      >
        위시등록
      </button>
      <button
        class="cancel__button"
        @click="_routeToGunplaWishList"
      >
        뒤로가기
      </button>
    </div>
  </div>
</template>

<script>
import DatePicker from '@/components/date-picker.vue'
import Constants from '@/Constants'
import { mapState } from 'pinia'
import { usersStore } from '@/store/users'
import sweetAlert from '@/wrapper/sweet-alert'

export default {
  name: 'user-gunpla-wish-create',
  components: { DatePicker },
  data () {
    return {
      types: Constants.TYPES,
      grades: Constants.GRADES,
      completes: Constants.Completes,
      figureInForm: {
        figureName: '',
        type: { code: '', value: '' },
        wishYn: false,
        nickName: null,
        version: null,
        series: null,
        grade: { code: '', value: '' },
        generation: null,
        price: 0,
        date: null,
        store: null,
        startDate: null,
        endDate: null,
        completeYn: { code: '', value: '' }
      }
    }
  },
  computed: {
    ...mapState(usersStore, ['userCustomInfo'])
  },
  methods: {
    _routeToGunplaWishList () {
      this.$router.push({ name: 'user-gunpla-wish-list' })
    },
    _clear () {
      this.figureInForm = {
        figureName: '',
        type: this.types[0].code,
        wishYn: false,
        nickName: null,
        version: null,
        series: null,
        grade: this.grades[0].code,
        generation: null,
        price: 0,
        date: null,
        store: null,
        startDate: null,
        endDate: null,
        completeYn: this.completes[0].code
      }
    },
    _saveWish () {
      if (this.figureInForm.figureName.replace(/ /g, '') === '') {
        sweetAlert.noIcon('이름은 필수 입력 사항입니다.', '확인')
      } else {
        const figureIn = {
          figureName: this.figureInForm.figureName,
          type: this.figureInForm.type,
          wishYn: true,
          nickName: this.figureInForm.nickName,
          version: this.figureInForm.version,
          series: this.figureInForm.series,
          grade: this.figureInForm.grade,
          generation: this.figureInForm.generation,
          price: String(this.figureInForm.price).replaceAll(',', ''),
          date: this.figureInForm.date,
          store: this.figureInForm.store,
          startDate: this.figureInForm.startDate,
          endDate: this.figureInForm.endDate,
          completeYn: this.figureInForm.completeYn
        }
        this.createFigure(this.userCustomInfo.userOid, figureIn).then(() => {
          this._clear()
          sweetAlert.noIcon(`${figureIn.figureName} 등록 되었습니다 :)`)
        })
      }
    }
  },
  mounted () {
    this.figureInForm.type = this.types[0].code
    this.figureInForm.grade = this.grades[0].code
    this.figureInForm.completeYn = this.completes[0].code
  }
}
</script>
<style scoped lang="scss">
.grid-item {
  height: 160px !important;
}
.button__wrapper {
  margin-top: 30px;
  text-align: right;
}
</style>
